<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="form"
      :model="entity"
      :rules="rules"
      autocomplete="off"
      layout="vertical"
    >
      <div class="card">
        <a-row :gutter="24">
          <a-col :sm="6">
            <a-form-model-item label="参数卡名称" prop="name">
              <a-input
                v-model="entity.name"
                placeholder="请输入参数卡名称"
                name="name"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <a-form-model-item label="版本" prop="version">
              <a-input
                v-model="entity.version"
                placeholder="请输入版本号"
                name="version"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="12">
            <a-form-model-item label="描述" prop="description">
              <a-textarea
                v-model:value="entity.description"
                placeholder="请输入描述信息"
                :rows="4"
                :max-length="1000"
                name="description"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
    </div>


        <g-collapse headerName="参数" >
            <template #content>
                <a-button type="dashed" style="margin-bottom:20px;" @click="addParameter">添加参数</a-button>

              <a-table :columns="parameterColumns" :row-key="record=>record.id" :data-source="entity.parameters" :pagination="false" bordered>
                  <template slot="key" slot-scope="record, index">
                    <a-input v-model:value="record.key" placeholder="参数名" />
                  </template>
                  <template slot="value" slot-scope="record, index">
                    <a-input v-model:value="record.value" placeholder="参数值" />
                  </template>
                  <template slot="operation" slot-scope="record, index">
                    <a-button type="danger" size="small" @click="removeParameter(index)">删除</a-button>
                  </template>
              </a-table>
            </template>
        </g-collapse> 
         
    </a-form-model>
  </a-spin>
</template>

<script>
import { GNdoPageEditBase } from "@/utils";
export default {
  name: "CreateOrEditProcessParameterCard",
  mixins: [GNdoPageEditBase],
  data() {
    return {
      entity: {
        name: "",
        version: "",
        description: "",
        parameters: [],
      },
      rules: {
        name: [{ required: true, message: "请输入参数卡名称", trigger: "change" }],
        version: [{ required: true, message: "请输入版本号", trigger: "change" }],
      },
       parameterColumns: [
        { title: "参数名", dataIndex: "key", key: "key", scopedSlots: { customRender: "key" },    },
        { title: "参数值", dataIndex: "value", key: "value", scopedSlots: { customRender: "value" }, },
        { title: "操作", dataIndex: "operation", key: "operation", scopedSlots: { customRender: "operation" }, },
      ],
    };
  },
  methods: {
    init() {
      if (this.id) {
        // 根据ID获取数据并填充表单
        this.loading = true;
        setTimeout(() => {
          // 模拟数据
          this.entity = {
            id: this.id,
            name: "参数卡" + this.id,
            version: "v1." + this.id,
            description: "描述" + this.id,
            parameters: [
              { key: "param1", value: "value1" + this.id },
              { key: "param2", value: "value2" + this.id },
            ],
          };
          this.loading = false;
        }, 500);
      } else {
        this.reset();
        this.loading = false;
      }
    },
    resetFormData() {
      this.entity = {
        name: "",
        version: "",
        description: "",
        parameters: [],
      };
    },
    async save() {
      let res = await this.checkError();
      if (res) {
        // ... 执行保存操作 ...
         this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.saveSuccess(this.entity.name);
          this.handleSaveEndBackView(this.id);
          this.refreshParent({
            reset: null,
          });
        });
      }
    },
     addParameter() {
      this.entity.parameters.push({ key: "", value: "", id: new Date().getTime() });
    },
    removeParameter(index) {
      this.entity.parameters.splice(index, 1);
    },
  },
};
</script>

<style lang="scss" scoped>
</style>